<!--  DOM  -->
<template>
    <div class="heads">
        <div class="top-text" >
            <div class="top-left" >
            <div class="img-bor" >
                <img src="../assets/img/pic-guanywm.png@2x.png" alt=""  @click="jumps" >
            </div>
                {{headTxt}}
            </div>
            <div class="top-left" >
            <div v-if="names" ><span @click="tologo" ></span>&nbsp&nbsp<span @click="toenroll" ></span></div> 
                <el-dropdown  v-else trigger="click"   >
                <span class="el-dropdown-link">
                    {{name}}<i class="el-icon-arrow-down el-icon-caret-bottom"></i>
                </span>
                <el-dropdown-menu slot="dropdown" style="z-index:2050" >
                    <el-dropdown-item @click.native="user()" ></el-dropdown-item>
                    <el-dropdown-item @click.native="tocol()" ></el-dropdown-item>
                    <el-dropdown-item @click.native="amend()" ></el-dropdown-item>
                    <el-dropdown-item @click.native="order()" ></el-dropdown-item>
                    <el-dropdown-item @click.native="quit()"  ></el-dropdown-item>
                </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'heads', // 
    data() {
        return {
            // 
            names:true,
            name:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        headTxt:{
            default:''
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        if(sessionStorage.getItem('xtoken') || localStorage.getItem('Xtoken')) {
            this.name = localStorage.getItem('Bname')
            this.names = false
        } else {     
            this.names = true
        }
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        jumps() {
            this.$router.push('/')
        },
        order() {
            this.$router.push('/user/order')
        },
        tologo() {
            this.$router.push('/logo')
        },
        toenroll() {
            this.$router.push('/enroll')
        },
        user() {
            this.$router.push('/user')
        },
        tocol() {
            this.$router.push('/user/fcoll')
        },
        amend() {
            this.$router.push('/user/root')
        },
        quit() {
            sessionStorage.removeItem('xtoken')
            localStorage.removeItem('Bname')
            localStorage.removeItem('Xtoken')
            localStorage.removeItem('valid')
            localStorage.removeItem('permissions')
            this.$router.replace({
                path: '/home',
            })
        },
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
.heads{
    // height: 40px;
    background-color: #FE6603;
    .el-dropdown-link{
      color: #fff;
    }
    .top-text {
        width: 1020px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        height: 40px;
        .top-left {
        cursor: pointer;
        height: 40px;
        display: flex;
        align-items: center;
        color: #fff;
        font-size: 12px;
        .img-bor {
            cursor: pointer;
            height: 24px;
            border-right: 1px solid #fff;
            padding-right: 10px;
            margin-right: 10px;
        }
        }
    }
}
</style>



