<template>
<div class="synth">
  <div class="syn-top" >
    <span :class="query ? 'current' : '' "  @click="changce" ></span>/<span :class="record ? 'current' : '' "  @click="changces" ></span>
  </div> 
 <!--   query.vue  -->
  <!-- <q-uery v-show="query" ></q-uery>    -->
  <!--   -->
  <!-- <h-is  v-show="record" > -->
    <router-view   :state='area'/>
</div>
</template>
<script>

export default {
  data () {
    return {
      query:true,
      record:false,
      area:'',
      areas:[
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
        {
          name:''
        },
      ],
      options:[],
      terrace:'',
      date:'',
      name:'',
      allarr:[]
    }
  },
  props: {
    state:''
  },
  watch: {
    state(val) {
      this.area = val
    }
  },
  methods: {
    changce() {
      this.query = true
      this.record = false
      this.$router.push('query')
    },
    gainhistory() {
      if(!sessionStorage.getItem('xtoken') || !localStorage.getItem('Xtoken') ) {
        return false
     }
     history({pageNo:1,pageSize:1000}).then(res => {
       if(res.code == 1) {
         this.allarr = res.data
       }
     })
    },
    changces() {
    if(sessionStorage.getItem('xtoken') || localStorage.getItem('Xtoken') ) {
        this.query = false
       this.record = true
       this.$router.push('hist')
     } else {
         this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$router.push('/logo')
        })
        return false
     }
     
    }
  },
  created () {
    this.gainhistory()
  },
  components: {
  }
}
</script>
<style lang="less" scoped>
.synth {
  width: 1020px;
  margin: 0 auto ;
  min-height: calc(~'100vh - 450px');
  padding-top: 80px;
  .syn-top {
    font-size: 16px;
    color:#999;
    margin: 20px 0 20px 0; 
    font-weight: 550;
    span {
      cursor: pointer;
    }
    .current {
      color:#333;
    }
  }
  .syn-query {
    width: 100%;
    box-sizing: border-box;
    height: 500px;
    background-color: #fff;
    margin-bottom: 200px;
    padding: 15px;
    .select {
      font-size: 16px;
      margin-bottom: 12px;
       .pro {
         li {
           padding: 2px 9px;
           height: 20px;
           text-align: center;
           line-height: 20px;
           margin-bottom: 6px;
           cursor: pointer;
           color:#666;
         }
         .bid-p {
           padding: 2px 9px;
           color:#666;
         }
         .current {
           background-color: #FE6603;
           color:#fff;
         }
       }
       .se-center {
         line-height: 40px;
       }
       .se-per {
           width: 50%;
           font-size: 14px;
           color:#999;
           display: flex;
           flex-direction: column; 
           .m-17 {
             margin-bottom: 17px;
           }
           input {
               height: 40px;
               line-height: 40px;
               border: 1px solid #dcdfe6;
               border-radius: 4px;
               padding: 0 15px;
               font-size: inherit;
           }
            .name-put {
                   width: 300px;
            }
           input::-webkit-input-placeholder {
               color:#999;
           }
       }
    }
  }
}

</style>



